import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import link_n_life from '@/views/link_n_life.vue'
import MotherView from '@/views/MotherView.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  // {
  //   path: '/',
  //   name: 'MotherView',
  //   component: MotherView,
  //   children: [
  //     {
  //       path: 
  //     }
  //   ],
  // },
  {
    path: '/contact',
    name: 'contact',
    component: () => import('@/components/LNL/contact.vue')
  },
  {
    path: '/shopShchet',
    name: 'shopShchet',
    component: () => import('@/components/LNL/shopShchet.vue')
  },
  {
    path: '/youtube',
    name: 'youtube',
    component: () => import('@/components/LNL/youtube.vue')
  },
  {
    path: '/News',
    name: 'News',
    component: () => import('@/views/News.vue')
  },
  {
    path: '/About',
    name: 'About',
    component: () => import('@/views/About.vue')
  },
  {
    path: '/shopGoods',
    name: 'shopGoods',
    component: () => import('@/views/shopGoods.vue')
  },
  {
    path: '/shopLife',
    name: 'shopLife',
    component: () => import('@/views/shopLife.vue')
  },
  {
    path: '/Object',
    name: 'Object',
    component: () => import('@/views/Object.vue')
  },
  {
    path: '/',
    name: 'linkNlife',
    component: link_n_life
  },

  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about11',
    name: 'about11',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
  // {
  //   path: '/',
  //   name: 'home',
  //   component: HomeView
  // },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }},
  routes
})

export default router
